import { BadgeComponent } from './../../widget/badge/badge.component';
import { CustomRenderComponent } from './../../core/render/custom-render/custom-render.component';
import { UserService } from './../../core/service/controlPanel/user/user.service';
import { ApiStatus } from 'src/app/core/constants/constants';
import { SubSink } from 'subsink';
import { IdRenderComponent } from './../../core/render/id-render/id-render.component';
import { iconRender, idRender, Userid } from './../../core/interface/common.interface';
import { IconRenderComponent } from './../../core/render/icon-render/icon-render.component';
import { Router } from '@angular/router';
import { tableOption, tableConfig } from './../../core/constants/constants';
import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';

@Component ({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.scss']
})
export class UserComponent implements OnInit  , OnDestroy {
  @ViewChild ('exportModal') exportModal: any;

  @ViewChild ('empModal') empModal: any;

  public tableOption = tableOption;

  public userModal = false;

  public tableLimitOption = tableConfig.tableLimitOption;

  public tableLimit = tableConfig.tableLimit;

  public tableSearch = '';

  public currentPage = 1;

  public gridData:grid[];

  public totalCount = tableConfig.pager;

  public itemsPerPage = tableConfig.pager;

  public employeeDetail = {
    id: 0,
  user_id: '',
  first_name: '',
  last_name: '',
  username: '',
  email: '',
  designation: '',
  license_type: '',
  user_group_name: '',
  first_time_login: ''
  }

public tableSettings = {
    column: {
      id: {
        title: 'ID',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'custom',
        renderComponent: IdRenderComponent,
        valuePrepareFunction: (value :idRender, row) => {
          value = {
              id: row?.id,
              color: '#3298DB',
              event: 'view',
              row: row,
          };
          return value;
        }
      },
      user_id: {
        title: 'User Id',
        icon: '',
        type: 'text',
      },
  first_name: {
    title: 'First Name',
    icon: '',
    type: 'text',
  },
  last_name: {
    title: 'Last Name',
    icon: '',
    type: 'text',
  },
  username: {
    title: 'Username',
    icon: '',
    type: 'text',
  },
  email: {
    title: 'Email',
    icon: '',
    type: 'text',
  },
  designation: {
    title: 'Designation',
    icon: '',
    type: 'text',
  },
  license_type: {
    title: 'License Type',
    icon: '',
    clickable: true,
    class: 'pointer',
    type: 'custom',
    renderComponent: CustomRenderComponent,
    valuePrepareFunction: (value, row) => {
      value = row?.license_type?.value;
       return value;
    }
    },
  user_group_name: {
    title: 'User Group Name',
    icon: '',
    type: 'text',
  },
  first_time_login: {
  title: 'First Time Login',
  icon: '',
  clickable: true,
  class: 'pointer',
  type: 'custom',
  renderComponent: BadgeComponent,
  valuePrepareFunction: (value, row) => {
    value = {
      color: row?.first_time_login?.color,
      value: row?.first_time_login?.value,
      fontColor: 'white'
    };
    return value;
  }
  }
  }
}

  private subs = new SubSink ();

  constructor( private _router: Router ,private _userService: UserService) { }

ngOnInit(): void {
    this.gridData = [];
    this.userGrid ( );
  }

tablePageChange(offSet: number) {
    this.currentPage = (offSet);
    this.userGrid ( offSet , this.tableSearch, this.tableLimit);
  }

 userGrid( offSet = 1, search = this.tableSearch, limit = this.tableLimit) {
    this.currentPage = offSet;
    const gridParams = {
      q: search,
      offset: offSet,
      limit: limit,
    };
    this.subs.add (
      this._userService.grid ( gridParams).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          this.gridData = res.results;
          this.totalCount =  res.count;
        }
      })
    );
  }

  search() {
    this.currentPage = 1;
    this.totalCount = this.tableLimit;
    this.userGrid ();
  }

openModal(){
    this.userModal = true;
    this.empModal.show ();
  }

  closeModal() {
    this.empModal.hide ();
    this.userModal = false;
  }

  tableEvent(event) {

if (event?.event === 'view' ) {
  this.employeeDetail = {
    id: event?.row?.id,
    user_id: event?.row?.user_id,
    first_name: event?.row?.first_name,
    last_name: event?.row?.last_name,
    username: event?.row?.username,
    email: event?.row?.email,
    designation: event?.row?.designation,
    license_type: event?.row?.license_type?.value,
    user_group_name: event?.row?.user_group_name,
    first_time_login: event?.row?.first_time_login
  };
  this.openModal ();
}
  }

  create() {
    this._router.navigate (['/employee-details/create']);
  }

  openExportPopUp() {
    this.exportModal.show ();
  }

closeExportModal() {
    this.exportModal.hide ();
  }

  downloadCsv() {
   // window.open (this._userService.employeeCsv (), '_self');
  }

downloadXls() {
   // window.open (this._userService.employeeXls (), '_self');
  }

ngOnDestroy() {
    this.subs.unsubscribe ();
  }
}

interface grid{
  id: number;
  user_id: string;
  first_name: string;
  last_name: string;
  username: string;
  email: string;
  designation?: any;
  license_type: Licensetype;
  user_group_name?: any;
  first_time_login: Firsttimelogin;
}

interface Firsttimelogin {
  value: string;
  color: string;
}

interface Licensetype {
  id: number;
  value: string;
}
