import { ApiEndpoints } from './../../../constants/constants';
import { Injectable } from '@angular/core';
import { HttpService } from '../../common/http.service';

@Injectable ({
  providedIn: 'root'
})
export class UserService {


  constructor(private http: HttpService,
    ) { }

grid(gridParams = { q: '', offset: 0, limit: 10}) {
        const keys: any = Object.keys (gridParams);
        const value: any =  Object.values (gridParams);
          let query = '?';
          for (let i = 0; i < keys.length; i++) {
            if (value[i] !== '') {
              query += keys[i] + '=' + encodeURIComponent (value[i]) + '&';
            }
          }
       return this.http.get (ApiEndpoints.allUserList+ query);
      }

}
